package ui.menus.toolsMenus

import kotlinx.browser.document
import kotlinx.html.div
import kotlinx.html.*
import kotlinx.html.dom.append
import kotlinx.html.js.onClickFunction
import org.w3c.dom.HTMLInputElement
import org.w3c.dom.asList
import org.w3c.dom.events.Event
import org.w3c.dom.get
import ui.menus.MenuItem
import ui.menus.MenuItemAction_Type

interface IToolsMenu {
    val parentElId   : String
    val menuItems       :List<MenuItem>
    val htmlID          :String

    fun spawnMenu() {

        document.getElementsByClassName("divToolsMenuPopup").asList().forEach { it.remove() }

        document.getElementById(parentElId)?.append {
            div("divToolsMenuPopup") {
                style = """
                    font-size: 0.8em;
                    position: absolute;
                    width: 10em;
                    padding: 1px;
                    transform:
                        translate(-10.2em);
                    background-color: #4a5;
                    border: 3px solid rgba(11,22,00,0.5);
                    box-shadow:
                        1px 1px 1px 3px rgba(9,9,9,0.4),
                        2px 2px 2px 5px rgba(9,9,9,0.2),
                        3px 3px 2px 6px rgba(9,9,9,0.04),
                """.trimIndent()
                ul {
                    style =
                        "list-style: none; max-width: 100%;  padding-inline-start: 0;     margin-block-start: 0.2em;   margin-block-end: 0.2em;"
                    menuItems.forEach {
                        li("liToolsMenuItem") {
                            style = "cursor: pointer; padding: 3px 1px 3px 1px; border-bottom: 1px solid rgba(88,99,77,0.4);"

                            onClickFunction = it.action as (Event) -> Unit

                            span {
                                style = "pointer-events: unset; font-size: 0.85em; white-space: nowrap; padding: 0; margin: 0; font-family: 'Rubik', sans-serif;"
                                +it.nameStr
                            }
                        }


                    }
                }
            }

        }
    }

    fun destroy() {
        document.getElementById(htmlID)?.remove()
    }
}



