package ui.shared_components

import kotlinx.html.*
import kotlinx.html.js.*
import kotlinx.html.js.onTouchEndFunction
import kotlinx.html.js.onTouchStartFunction
import org.w3c.dom.*
import ui.menus.toolsMenus.PlaylistToolsMenu
import ui.menus.toolsMenus.*


fun <T> TagConsumer<HTMLElement>.dataListView(
    title: String,
    dataList :Collection<T>,
    cardEach: DIV.(cardData:T) -> Unit
) {

    div("dataViewHolder") {
        dataViewTitleBar(title)
        dataViewInsetList(dataList, cardEach)
    }
}

private fun DIV.dataViewTitleBar(title: String) {

    fun DIV.imgToolsMenuIcon() {
        div {
            id = "divToolsMenuIcon"

            style = """
                        height: 1.5em;
                        width:  1.5em;
                          
                        text-align: right;
                        display: inline-block;
                        padding: 0; margin: 0.5% 0 0 0;
                    """




            img(classes="imgToolsMenuIcon") {
                src = "/spotify/res/toolsMenuIcon.png"
                style = """
                            border: thin solid rgba(19,19,19,0.45);
                            width:100%;
                            padding: 0; margin: 0; 
                            

                        """}

                onClickFunction = {

                    PlaylistToolsMenu(
                        this.id,
                    ).spawnMenu()

            }
        }
    }


    div("divDataViewTitle") {
        style = "background-color: rgba(88,88,105,0.25); text-align: right;" +
                "padding: 0.2em; margin: 0.1em 4px 1px 3px;" +
                "border: 1px solid rgba(5,5,5,0.1); border-bottom: none;" +
                """box-shadow: 3px 3px 0 4px rgba(3,3,1,0.1), 1px 2px 1px 1px rgba(13,13,11,0.75), inset 2px 2px 0 1px rgba(255,255,253,0.09);""".trimMargin()
        h4("h4DataViewTitle") {
            style = """ text-align: left; display: inline-block; width: 85%; margin: 0;"""
            +title
        }

        imgToolsMenuIcon()
    }
}


private fun <T> DIV.dataViewInsetList(
    dataList: Collection<T>,
    cardEach: DIV.(cardData: T) -> Unit
) {
    div("dataviewlist") {
        dataList.forEach {
            style = """
                     box-shadow:
                        inset 2px 2px 0.45em 0.15em rgb(25 60 90 / 70% ),
                        inset 4px 5px 0.8em 2.5em rgb(25 50 60 / 8% );
                     padding: 0.15em;
                     margin: 0.45em; margin-top: 0.05em;
                     background-color: rgb(50 45 65 / 8%);
                     max-height: 66vh; overflow-y: auto;
                """
            div("card") {
                onTouchStartFunction = {
                    it.target.asDynamic().style.boxShadow =
                        "inset 0.1em 0.1em 0.3em -0.03em rgba(20,20,20,0.6)"
                }
                onTouchEndFunction = {
                    it.target.asDynamic().style.boxShadow = "none"
                }


                cardEach(it)
            }
        }


    }
}
