package ui.menus.toolsMenus

import kotlinx.browser.document
import org.w3c.dom.asList
import toggleHidden
import ui.menus.MenuItem
import ui.menus.MenuItemAction_Type


class PlaylistToolsMenu(
    override val parentElId: String,
) : IToolsMenu {

    override val htmlID get() = "playlistToolsMenu"

    override val menuItems: List<MenuItem> get() = listOf(

        MenuItem("Audio features", MenuItemAction_Type.CHECKBOX) {
            console.log("clicked: toggle audio feats")
            document.getElementsByClassName("ulFeatures").asList()
                .forEach { e ->
                    e.toggleHidden()
                }
        },

        MenuItem("Musical features", MenuItemAction_Type.CHECKBOX) {
            console.log("track feats")
        },

        MenuItem("Export training data", MenuItemAction_Type.BUTTON) {
            console.log("export training data")
            document.getElementsByClassName("divTrackCard_exportTrainingData").asList()
                .forEach { e ->
                    e.toggleHidden()
                }
        },
    )






}
